@import url('https://fonts.googleapis.com/css2?family=Wix+Madefor+Display:wght@400..800&display=swap');
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(0, 120, 116);
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.navb h3{
  margin-top: 18px;
  margin-right: 9px;
  margin-left: 7px;
  color: rgb(0, 211, 183);
}
.navb{
  position: fixed;
  z-index: 1;
  padding: 0px;
  box-shadow: 1px 1px 10px rgba(0,0,0,0.2);
  background-color: rgba(30, 33, 45, 0.960);
  color: aliceblue;
  display: flex;
  top: 0px;
  width: 100%;
}
.navli a:hover,
.navli a.active {
  transition: 0.3s;
  color: rgb(0, 197, 161);
}
.navb li a::after{
  content: "";
  width:0%;
  height:3px;
  background-color: rgb(0, 197, 161);
  position: absolute;
  bottom: 4px;
  left: -10px;
  right: -10px;
  transition: 0.3s;
}
.navb li a:hover::after{
  width: 60%;
  left:10px
}
.navb li a.active::after{
  content: "";
  height: 3px;
  width: 82%;
  left: 10px;
}
.navli{
  display: inline;
  margin: 8px 15px;
  position: relative;
  list-style: none;
  padding: 10px;
  font-size: 17px;
  text-decoration: 'none'
}
.lin{
  color: inherit;
  text-decoration: none;
}
li:first-child{
  margin-left: 30px;
}
.butcard{
  background-color: rgb(4, 170, 109);
  color: white;
  text-decoration: none;
  border-radius: 3px;
  padding: 8px 18px;
  border: none;
  margin: 5px;
  position: relative;
  transition: 0.3s;
}
.butcard:hover{
  background-color: rgb(5, 87, 57);
}
.container{
  width: 1000px;
}
#pre{
  position: absolute;
  left:18%;
}
#nex{
  position: absolute;
  left: 75%;
}

.pagebut{
  background-color: rgb(30, 33, 45);
  color: white;
  padding: 10px 20px;
  margin: 10px 10px;
  text-decoration: none;
  border-radius: 3px;
  border: none;
  transition: 0.3s;
}
.pagebut:hover{
  background-color: black;
}
h1{
  position: relative;
  text-align: center;
  margin-top: 80px;
  color: rgb(224, 224, 224);
}
.load{
  align-items: center;
  position: relative;
  left:44%;
}
.read-more {
  position: absolute;
  bottom: 10px;
  right: 10px;
}
.news-card {
  position: relative;
  display: flex;
  border: 1px solid #ccc;
  margin-top: 7px;
  margin-bottom: 20px;
  padding: 10px 10px;
  left: 25%;
  background-color: rgb(255, 255, 255);
  box-shadow: 1px 1px 30px rgba(0, 0, 0, 0.1);
  transition: 0.3s;
  border-radius: 25px;
}
.news-card:hover{
  transform: scale(1.02);
}

.image-container {
  flex: 0 0 auto; 
}

.text-container {
  flex: 1; 
  padding: 0px 10px;
}

.imgcard {
  max-width: 300px;
  height: 200px;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.5);
  border-radius: 25px;
  transition: 0.3s;
}
.newstitle{
  font-size: 20px;
  margin: 10px 0px;
}
.newsdesc{
  margin-top: 0px;
}
.source{
  background-color: rgb(203, 0, 0);
  color: white;
  padding: 8px;
  margin: 12px 8px;
  border-radius: 5px;
  font-size: 12px;
  position: absolute;
  right: -3%;
  top: -8%;
}
.newtime{
  background-color: rgba(65, 65, 65, 0.485);
  padding: 5px;
  display: inline-block;
  font-size: 12px;
  position: absolute;
  bottom: 5px;
  color: rgba(255, 255, 255, 0.8);
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.headcat{
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: rgb(30, 33, 45);
}